
.cookies-notification{

  //display: none;

  &.alert{
    background-color: transparentize($global-sitebg-darkblue01, 0.10);
    color: $global-clr-white;
    font-size: 17px;
    position: fixed;
    width: 100%;
    padding: 0;
    margin-bottom: 0;
    border: none;
    @include border-radius(0);
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
  }

  &-body{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;

    &-desc{
      a{
        color: $global-clr-white;
        text-decoration: underline;
      }
    }
    &-btn{}
  }
}